*, *::before, *::after {
    box-sizing: border-box;
  }
  
  body {
    margin: 0;
  }
  
  .container {
    background: linear-gradient(rgb(233, 220, 226) 0%, rgb(101, 93, 92) 100%);
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr auto;
    grid-template-areas: 
      "image sidebar"
      "slider sidebar";
    height: 100vh;
    justify-items: center;
    background-color: #DADADA; 
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    /* width: 100vw; */
    /* padding-top: 1rem;   */
}
  
  .main-image {
    grid-area: image;
    background-image: url("https://images.unsplash.com/photo-1570641963303-92ce4845ed4c?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8OXx8bmF0dXJlJTIwYmFja2dyb3VuZHxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=500&q=60");
    width: 60%;
    height: 90%;
    background-position: top center;
    background-size: contain;
    background-repeat: no-repeat;
    margin: 2rem 0 0 0 ;
    padding:1rem 0 1rem 0;
    background-color: rgb(255, 255, 255);
    border-radius: 12px;
    display: grid;
    box-shadow: 2px 2px 13px rgb(73, 72, 72);
    
  }
  
  .sidebar {
    background-color: hsl(0, 0%, 23%);
    border-left: 4px solid hsl(0, 94%, 94%);
    margin:  2rem 2rem 2rem 0;
    padding:2rem;
    display:grid;
    border-radius: 12px;
    box-shadow: 4px 2px 13px rgb(73, 72, 72);
    
    
    /* grid-area: sidebar; */
    /* display: flex; */
    /* height: auto; */
    /* width:30%; */
    /* margin-top: 1rem; */
    /* flex-direction: column; */
    /* align-items: stretch; */
  }
  
  .sidebar-item {
    cursor: pointer;
    border: none;
    outline: none;
    background-color: hsl(0, 0%, 32%);
    padding: 3rem ;
    position: relative;
    margin-right: 1rem;
    transition: background-color 150ms;
    display: grid;
    align-items: center;
    color:azure;
    flex-direction: column;
    justify-items: center;
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    border-radius: 12px;
    box-shadow: 2px 2px 13px rgb(73, 72, 72);
    
  }
  
  .sidebar-item:hover, .sidebar-item:focus {
    background-color: hsl(0, 0%, 100%);
    color:rgb(60, 60, 60);
  }
  
  .sidebar-item.active {
      background-color: hsl(0, 94%, 94%);
      color:rgb(60, 60, 60);
      
    }
    .sidebar-item svg {
        margin-bottom: 1rem;
    }

  
  .sidebar-item::after {
    content: '';
    position: absolute;
    width: 80%;
    left: 10%;
    bottom: 0;
    height: 1px;
    background-color: hsl(0, 0%, 18%);
  }
  
  .sidebar-item:last-child::after {
    display: none;
  }
  
  .slider-container {
    grid-area: slider;
    margin-bottom: 2rem;
    width: 90%;
    display: flex;
    flex-direction: row;
    /* padding: 1rem; */
    
  }
  
  .slider {
    width: 100%;
    cursor: pointer;
    margin-right: 1rem;
  }