body{
    font-family: 'Poppins', sans-serif;
}

.component{
  
   
    height: auto;
    padding: 7rem;
    display:flex;
    flex-direction: column;
    
    position:relative;
    height:calc(100vh - 4rem);
    
}
.mainimage{
    position:absolute;
    width:100%; 
    height:calc(100vh - 2rem);
    overflow: hidden;
    display:flex;
    flex-direction: row;
   
}
p{
    font-size: 3em;
    color:antiquewhite;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
   
}
.mainpageButton{
    position:relative;
    border: 2px solid rgb(53, 154, 232);
    background-color: rgb(53, 154, 232);
    padding:1rem 2rem 1rem 2rem;
    border-radius: 50px;
    width:13rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: larger;   
}
.mainpageButton:hover{
    background-color: transparent;
    color:white;
}
code{
    font-size: small;
    color:blanchedalmond;
    margin-top: 2rem;

}
mark{
    
    color:rgb(0, 174, 255);
    background-color: transparent;
}

